import React from 'react';
import styled from 'styled-components';
import { ButtonStyle } from '../Reusable/Buttons/StartGameButton';

const EndGameWrapper = styled.div`
position: absolute;
top: 0;
bottom: 35%;
left: 0;
right: 0;
background-color:${({ theme }) => theme.colors.deepRed};
transform: ${({ isGameEnded }) => isGameEnded ? 'translateY(0)' : 'translateY(-100%)'};
transition: .4s .4s linear all;
color: ${({ theme }) => theme.colors.gray};
font-size: 1rem;
h2,h3{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    width: 80%;
}
h2{
    top: 10%;
}
h3{
    top: 40%;
}
`
const WinOrLoseMessageWrapper = styled.h2`
font-size: 1.6em;
`
const GuessedPhraseWrapper = styled.h3`
font-size: 1.2em;

span{
    color:${({ theme }) => theme.colors.winGamePhrase};
    font-weight: 700;
    font-size: 1.6em;
    display: block;
    margin-top: 10%;
    letter-spacing: 1px;
}
`


const ButtonEndGameStyle = styled(ButtonStyle)`
top: 80%;
height: 4em;
width: 12em;
font-size: 1.2;
border-radius: 50px;
background-color: ${({ theme }) => theme.colors.deep};
color: ${({ theme }) => theme.colors.winGamePhrase};
transition:none;
`


const EndGamePage = ({ isGameEnded, isGameWon, phraseToGuess, startGame }) => {
    const titbit = 'hh'
    const winOrLoseMessage = isGameWon ? 'Good job!' : `Don't give up, try again!`
    const guessedPhrase = !isGameWon && phraseToGuess.map(phrase => phrase.letter);

    const winOrLoseContent = <>
        <WinOrLoseMessageWrapper>
            {winOrLoseMessage}
        </WinOrLoseMessageWrapper>

        {isGameWon ? titbit : <> <GuessedPhraseWrapper>
            The correct phrase was: <span>{guessedPhrase}</span>
        </GuessedPhraseWrapper>
        </>}

    </>



    return (
        <EndGameWrapper
            isGameWon={isGameWon}
            isGameEnded={isGameEnded}>

            {isGameEnded && winOrLoseContent}

            <ButtonEndGameStyle
                onClick={startGame}
                phraseToGuess={phraseToGuess}>Play again
            </ButtonEndGameStyle>

        </EndGameWrapper>
    );
}
export default EndGamePage;